import Vue from 'vue'

new Vue({
	data() {
		return {
			message: 'Hello World!'
		}
	}
}).$mount('#app')

const fullpage = require('./fullpage')

new fullpage('#fullpage', {
	navigation: true,
	menu: '.fixed-top',
	responsiveWidth: 700,
	anchors: ['welcome', 'about', 'sale', 'service', 'contact'],
	onLeave: function (origin, destination, direction) {
		console.log("Leaving section" + origin.index);
	},
});



document.addEventListener('DOMContentLoaded', function () { // Аналог $(document).ready(function(){
	console.log('done!')
});
